import type { Metadata } from "next";
import { Playfair_Display, Noto_Serif_Thai } from "next/font/google";
import "./globals.css";

const notoSerifThai = Noto_Serif_Thai({
    subsets: ["thai", "latin"],
    weight: ["300", "400", "500", "600", "700"],
    variable: "--font-noto-serif",
    display: 'swap',
});

const playfair = Playfair_Display({
    subsets: ["latin"],
    variable: "--font-playfair",
    display: 'swap',
});

export const metadata: Metadata = {
    title: "SAMREDROOB - ผ้าม่านและวอลเปเปอร์เกรดพรีเมียม",
    description: "คำนวณราคาและเลือกชมสินค้าคุณภาพเยี่ยมของเรา",
};

export default function RootLayout({
    children,
}: Readonly<{
    children: React.ReactNode;
}>) {
    return (
        <html lang="th">
            <body className={`${playfair.variable} ${notoSerifThai.variable} font-serif antialiased`}>
                {children}
            </body>
        </html>
    );
}
